<template>
  <div class="flex flex-col items-center gap-4">
    <form class="flex w-72 flex-col gap-2">
      <p>
        Please scan this QR code with 2FA Auth App (e.g.
        <a
          href="https://apps.apple.com/us/app/google-authenticator/id388497605"
          target="_blank"
          >Google Authenticator</a
        >) to create time-based two-factor code:
      </p>

      <div class="overflow-hidden rounded-md border">
        <img
          :src="secrets.qr_code_blob"
          width="100%"
        />
      </div>

      <p>
        If you can't scan a code, please use this string as a key: <b>{{ secrets.secret }}</b>
      </p>

      <v-button
        variant="primary"
        class="mt-2"
        :to="`/auth/2fa/verify/${secrets.ticket}`"
      >
        Continue
      </v-button>
    </form>

    <p class="text-sm">
      <NuxtLink to="/auth">Sign In</NuxtLink>
    </p>
  </div>
</template>

<script setup lang="ts">
import { get2FAEnableSecrets } from "~/api/auth"

definePageMeta({
  requiredRole: "guest",
  layout: "centered",
})

const route = useRoute()
const secrets = await get2FAEnableSecrets({ ticket: route.params.ticket as string })
</script>
